.header {

  &__container {
    height: 63vh;
    @include flex-column-center;
    background-color: $decorColor1;
    padding: 15px 15px 0;
  }
}

@media (min-width: 768px) {
  .header {
    &__container {
      background-image: linear-gradient(115deg, $decorColor1 0% 78%, $bgColor1 78% 100%);
    }
  }
}
